.projectCard {
    border: var(--purple) 4px solid;
    height: auto;
    border-radius: 25px;
  }
  
  .project-title {
    text-align: center;
    font-weight: bolder;
    font-size: 3.5rem;
    color: white;
    margin-top: .5rem;
    margin-bottom: 1.5rem;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  
  }
  img {
    width: 100%;
    height: 100%; 
  }
  
  .project-description {
    font-weight: bolder;
    font-size: 3rem;
    color: var(--pink-little-red);
  }
  .project-description-paragraph {
    font-weight: bold;
  }
  .projectBtn {
    background-color: var(--magenta);
    border: var(--purple) solid 5px;
    width: 100%;
    margin-right: 5px;
    margin-bottom: 5px;
    border-radius: 25px;
  }
  
  .projects-title-text {
    font-size: 4rem;
    font-weight: bolder;
    color: var(--pink-little-red);
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
    text-align: center; 
  }
  

  
  .onlyPlants-title {
    text-align: center;
  font-weight: bolder;
  font-size: 3.5rem;
  color: white;
  margin-top: .5rem;
  margin-bottom: 1.5rem;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  }

  .built-with-icon {
    transform: scale(3.5);
  }

  .word-logo{
    margin-left: .75rem;
  }
  
